import p1 from './assets/p1.png';
import p2 from './assets/p2.png';
import p3 from './assets/p3.png';
import './App.css';
import { PlusOutlined, MinusOutlined } from '@ant-design/icons';
import { Button, Col, Row } from 'antd';
import { useState } from 'react';
import ScaleText from "react-scale-text";

function App() {
  const [time, setTime] = useState(1634)


  const minusTime = () => setTime(time-1)
  const plusTime = () => setTime(time+1)
  const p1Time = () => setTime(time-152)
  const p2Time = () => setTime(time-126)
  const p3Time = () => setTime(time-100)

  return (
    <div className="App">
      <Row>
        <ScaleText widthOnly={true}>
          <span>
          {Math.floor(time/60)}:{time % 60}
          </span>
        </ScaleText>
        
      </Row>
      <Row>
        <Button  style={{height: '100px'}} onClick={p1Time}>
          <img src={p1} alt="phase1" />
        </Button>
      </Row>
      <Row style={{marginTop: '20px'}}>
        <Button style={{height: '100px'}} onClick={p2Time}>
          <img src={p2} alt="phase2" />
        </Button>
      </Row>
      <Row style={{marginTop: '20px'}}>
        <Button style={{height: '100px'}} onClick={p3Time}>
          <img src={p3} alt="phase3" />
        </Button>
      </Row>

      <Row gutter={[10,10]} style={{marginTop: '20px'}}>
        <Col span={12}>
          <Button type="primary"  icon={<PlusOutlined />} style={{paddingTop: '40px', paddingBottom: '70px'}} block={true} onClick={plusTime}/>
        </Col>        
        <Col span={12}>
          <Button type="primary" icon={<MinusOutlined />}  style={{paddingTop: '40px', paddingBottom: '70px'}}block={true} onClick={minusTime}/>
        </Col>
        
      </Row>
    </div>
  );
}

export default App;
